<template>
  <v-card elevation="0" tile :color="bg_color" class="mx-5">
    <v-container class="py-0">
      <v-row justify="center">
        <v-card elevation="0" tile class="px-0" width="1200" :color="bg_color">
          <v-fab-transition>
            <v-btn
              :color="theme_color"
              dark
              absolute
              top
              left
              fab
              :x-large="!isSmall"
            >
              <v-icon color="white">{{ mian_icon }}</v-icon>
            </v-btn>
          </v-fab-transition>
          <v-card-text>
            <v-list class="mb-10" :color="bg_color">
              <v-subheader class="px-0 mx-0 mt-5">
                <v-row justify="center" class="px-0 mx-0">
                  <v-col cols="3">
                    <span
                      :class="
                        isSmall
                          ? theme_text_color + ' subtitle-1'
                          : theme_text_color + ' title'
                      "
                    >
                      Projects
                    </span>
                  </v-col>
                  <v-spacer></v-spacer>
                </v-row>
              </v-subheader>
              <v-row justify="center" class="px-0 mx-0">
                <v-col>
                  <p style="color: grey">
                    Some project demos may take a few seconds to start up as
                    they are deployed on free platforms, such as Heroku and
                    Netlify.
                  </p>
                </v-col>
              </v-row>

              <v-list-item-group active-class="border" :color="theme_color">
                <v-list-item
                  v-for="(item, index) of proj"
                  :key="index"
                  class="pa-0 py-1"
                  :href="item.url"
                  target="_blank"
                >
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-row v-bind="attrs" v-on="on">
                        <v-col sm="6" md="4" align-self="center" align="cnter">
                          <v-img
                            height="200"
                            width="400"
                            :src="item.img"
                            contain
                          >
                          </v-img>
                        </v-col>

                        <v-col sm="6" md="8">
                          <div class="subtitle-1">
                            <strong>{{ item.name }}</strong>
                          </div>
                          <div
                            class="subtitle-1 font-weight-light"
                            style="height: 100px"
                            v-if="window_size >= 900"
                            v-html="item.description"
                          ></div>
                          <div
                            class="subtitle-1 font-weight-light"
                            style="height: 100px"
                            v-else
                            v-html="item.description.slice(0, 100) + '...'"
                          ></div>
                        </v-col>
                      </v-row>
                    </template>
                    <span>Click to visit website.</span>
                  </v-tooltip>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card-text>
        </v-card>
      </v-row>
      <v-divider></v-divider>
    </v-container>
  </v-card>
</template>

<script>
export default {
  name: "education",
  created() {
    window.addEventListener("resize", this.cal_window);
  },
  destroyed() {
    window.removeEventListener("resize", this.cal_window);
  },
  mounted() {
    this.cal_window();
  },
  methods: {
    cal_window() {
      this.window_size = window.innerWidth;
      this.isSmall = window.innerWidth < 1264 ? true : false;
    },
  },
  data: () => ({
    mian_icon: "mdi-monitor-dashboard",
    bg_color: "white",
    theme_color: "deep-purple darken-4",
    theme_text_color: "deep-purple--text text--darken-4",
    isSmall: false,
    window_size: null,
    proj_index: 0,
    proj: [
      {
        name: "CO2 emission modeling in cities of developing countries (World bank project, ongoing)",
        description: `This project aims to develop a high-resolution and near real-time CO2 emission model 
        for dense residential areas in developing countries including Turkey, Egypt, and South America.`,
        url: "https://3d-earth-lsce.netlify.app/",
        img: require("@/assets/wb_proj.png"),
      },
      {
        name: "EU27&UK natural gas consumption analysis and Russia-Ukraine crisis",
        description: `In this project, we combine daily data of pipeline flow, production, storage, 
        and consumption of natural gas across EU27 countries and the UK to assess the magnitude of the Russian gas use. 
        We then investigate the magnitude of shortfalls if Russian imports were to stop, 
        and how those gaps could be filled due to the Russia-Ukraine crisis in 2022.`,
        url: "https://eugas.herokuapp.com/",
        img: require("@/assets/russian_gas.png"),
      },
      {
        name: "pyebas (Python package) for collecting and managing air pollution measurment data from EBAS",
        description: `pyebas is a Python package created for an easy-access to open-source air pollutant data from EBAS database via their FTP server. 
        EBAS database collects mainly from EU air pollutant monitoring programs.`,
        url: "https://pypi.org/project/pyebas/",
        img: require("@/assets/pyebas.png"),
      },
      {
        name: "Visualization and Modeling for Great Lakes Fish Monitoring and Surveillance Program",
        description: `The aim of this project is to provide research community and public a user-friendly  interface for accessing 
          Great Lakes Fish Monitoring and Surveillance Program data 
          , as well as data modeling tools. This project uses D3.js and Plotly.js for data visualizations, 
          Vue.js and Vuetify.js as web framework and UI, Python-flask as backend.`,
        url: "https://glfmsp-viz.herokuapp.com/",
        img: require("@/assets/glfmsp_vue.png"),
      },
      {
        name: "California WildFire",
        description: `The aim of this project is to provide a dashboard for California wildfire situations tracking. 
        This project uses data collected from <a style="color:grey" href="https://www.fire.ca.gov/" taget_="blank"> 
        California government website</a>. 
          The dashboard is created with D3.js, plotly.js, Vue.js and Vuetify.js.`,
        url: "https://wildfire-ca.herokuapp.com/",
        img: require("@/assets/ca_wildfire.png"),
      },
      {
        name: "TraPSA (Trajectory-based Potential Source Apportionment) Web Application",
        description: `The aim of this project is to provide a open-source toolkit for air pollution potential source location apportionment analysis 
          based on trajectory ensemble receptor models. This project uses NOAA HYSPLIT4 model to generate trajectory endpoints. 
          The web application uses Plotly.js, Vue.js and Vuetify.js as frontend, and Python as backend.`,
        url: "https://trapsa.herokuapp.com/",
        img: require("@/assets/trapsa_vue.png"),
      },
      {
        name: "COVID-19 Global Situation Trakcer",
        description: `The aim of this project is to provide a dashboard for COVID-19 global situations tracking. 
        This project uses <a style="color:grey" href="https://github.com/CSSEGISandData/COVID-19" taget_="blank"> 
        Johns Hopkins University Center for Systems Science and Engineering datasets </a>. 
          The dashboard is created with D3.js, plotly.js, Vue.js and Vuetify.js.`,
        url: "https://covid19-tracker-defve.herokuapp.com/",
        img: require("@/assets/covid_19.png"),
      },
      {
        name: "TraPSA (Trajectory-based Potential Source Apportionment) Software",
        description: `This project is the early version of TraPSA web application. 
          The software is developed with MATLAB.`,
        url: "https://webspace.clarkson.edu/projects/TraPSA/public_html/",
        img: require("@/assets/trapsa.png"),
      },
    ],
  }),
};
</script>
<style scoped>
/* div {
  outline: 1px dashed blue;
} */
</style>